@import "./vars.scss";

@import "~rfs/scss";

.slides {
  height: 100%;
}

h1 {
  @include font-size(4rem);
  @include rfs(4.5rem, line-height);
}
h2 {
  @include font-size(3.5rem);
  @include rfs(4rem, line-height);
}
h3 {
  @include font-size(3rem);
  @include rfs(3.5rem, line-height);
}
h4 {
  @include font-size(2.7rem);
  @include rfs(3.2rem, line-height);
}
h5 {
  @include font-size(2.3rem);
  @include rfs(2.8rem, line-height);
}
h6 {
  @include font-size(2rem);
  @include rfs(2.7rem, line-height);
}

a {
  color: #51c2f7;
}

.pos {
  position: absolute;

  @include rfs(2rem, bottom);
  @include rfs(2rem, left);

  @include rfs(4rem, font-size);
  @include rfs(4.5rem, line-height);
}
